body {
    --video-overly-text: rgb(99, 99, 99);
}
.videoContainer {
    position: relative;
    width: 100%;
    height: 100%;
    margin-top: 100px;
    background-attachment: scroll;
    overflow: hidden;
    max-width: 100%;
}       
.videoContainer video {
    /* min-width: 100%;
    min-height: 100%; */
    position: relative;
    z-index: 1;
}
.videoContainer .myGradient {
    height: 100%;
    /* width: 100%; */
    width: 15%;
    position: absolute;
    top: 0px;
    left: 30%;
    z-index: 2;
    /* opacity: 0.5; */
    background: linear-gradient(to right, black , rgba(255, 255, 0, 0));
    /* background: black; */
}
.banner {
    height: 100%;
    width: 30%;
    top: 0px;
    left: 0px;
    background-color: black; 
    position: absolute;
    z-index: 3;

}
video {
    width: 70%;
    min-width: 70%;
    left: 30%;
}
.videoTitle {
    font-family: 'Righteous', cursive;
    font-size: 4vw;
    color: var(--video-overly-text);
    z-index: 5;
}
.features {
    font-family: 'Righteous', cursive;
    font-size: 3vw;
    color: var(--video-overly-text);
    z-index: 5;
}
.visitProject {
    display: flex;
    
}